@import "ScssHelpers/helpers-all.scss";

.banner_image {}

.banner_image__wrap {
	display: flex;
	justify-content: flex-start;
	align-items: stretch;

	&.banner_image__wrap--reverse_mod {
		@include bp($point_2, min) {
			flex-direction: row-reverse;
		}
		@include bp($point_3) {
			flex-direction: column;
		}
	}

	@include bp($point_3) {
		flex-direction: column-reverse;
	}
}

.banner_image__content {
	margin: auto 0;

	@include bp($point_2, min) {
		width: 49%;
		padding: 1.7rem 0 1.7rem;
		flex-shrink: 0;

		&:not(.banner_image__wrap--reverse_mod &) {
			margin-right: 9.2rem;
		}
		.banner_image__wrap--reverse_mod & {
			margin-left: 9.2rem;
		}
	}
	@include bp($point_3) {
		.banner_image__wrap--reverse_mod & {
			&:not(:last-child) {
				margin-bottom: 2.5rem;
			}
		}
	}
}

.banner_image__title {
	@include h2;

	letter-spacing: -.017em;
	line-height: 1;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 2.4rem;
		}

		@include bp($point_3) {
			margin-bottom: 1.4rem;
		}
	}

	@include bp($point_3) {
    letter-spacing: -0.025em;
	}
}

.banner_image__text {
	@include text;


	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 3.4rem;
		}

		@include bp($point_3) {
			margin: 0 0 1.9rem 0;
		}
	}

	p:not(:last-child) {
		@include bp($point_2, min) {
			margin: 0 0 1rem 0;
		}

		@include bp($point_3) {
			margin: 0 0 .7rem 0;
		}
	}

	@include bp($point_2, min) {
		line-height: 1.48;
	}

	@include bp($point_3) {
		letter-spacing: -0.015em;
    line-height: 1.45;
	}
}

.banner_image__img_w {
	position: relative;

	display: block;
	width: 100%;
	height: 100%;

	@include bp($point_2, min) {
		max-width: 64rem;
		padding-bottom: 42%;
		margin-left: auto;
	}

	@include bp($point_3) {
		&:not(.banner_image__wrap--reverse_mod &) {
			margin-bottom: 2.5rem;
		}
	}

	@include bp($point_3) {
		padding-bottom: 81.1%;
	}

	@include bp($point_1, min) {
		&:not(.banner_image__wrap--reverse_mod &) {
			margin-right: -10.8rem;
		}
		.banner_image__wrap--reverse_mod & {
			margin-left: -10.8rem;
		}
	}
	
}

.banner_image__img {
	@include stretch;
	@include cover_img;
}

.banner_image__btn {
	display: inline-flex;

	&:not(:last-child) {
		margin-right: 2rem;
	}
}
